import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from '@/router'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading:null,
    busqueda:"",
    ingresoUsuario:null,
    nombreUsuario:null,
    rolUsuario:null,
    seguidoresUsuario:null,
    imagenUsuario:null,
    fecha_creacion_user:null,
    level_user:null,
    expericence_user:null,
    resultados:[],

  },
  getters: {
  },
  mutations: {
    cargar_datos(){
      var token_active = localStorage.getItem("key_user")
      if(token_active != null){
        axios.get(`/usuario/${token_active}`).then((response)=>{
          const usuario_token = response.data
          if(usuario_token != null){
            this.state.nombreUsuario = usuario_token.nombre_user
            this.state.seguidoresUsuario = usuario_token.seguidores
            this.state.imagenUsuario = usuario_token.img
            this.state.fecha_creacion_user = usuario_token.fecha_creacion
            this.state.level_user = usuario_token.nivel
            this.state.experience_user = usuario_token.puntos
            if(usuario_token.rol == "Administrador"){
              this.state.rolUsuario = "Administrador"
            }else{
              this.state.rolUsuario = "Usuario"
            }
            this.interval = setTimeout(() => {
              this.state.ingresoUsuario = true
              this.state.loading = false
            }, 1100)
          }
        }).catch((e) => {
          console.log("Token encontrado incorrecto")
          this.interval = setTimeout(() => {
            this.state.ingresoUsuario = false
            this.state.loading = false
            localStorage.clear()
            router.push("/").catch(error => {
              if (error.name != "NavigationDuplicated") {
                console.log("Navegacion Error")
              }
            });
          })
        })
      }else {
        console.log("token no encontrado")
        this.interval = setTimeout(() => {
          this.state.loading = false
          this.state.ingresoUsuario = false
        }, 1000)
      }
      this.interval = setTimeout(() => {
        this.state.loading=false
      }, 1300)
    }
  },
  actions: {
  },
  modules: {
  }
})
